import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useParams } from 'react-router-dom';
import { SET_DATA } from 'store/actions';

import CodeIcon from '@material-ui/icons/Code';
import CloudIcon from '@material-ui/icons/Cloud';
// material-ui
import { Grid, Button, Table, TableContainer, TableBody, TableHead, TableRow, TableCell, Typography, Slider, TextField, Chip } from '@material-ui/core';
import StockChartCard from 'views/dashboard/Default/StockChartCard';
import MainCard from 'ui-component/cards/MainCard';
// project imports
import { gridSpacing } from 'store/constant';

import StockCard from 'views/dashboard/Default/StockCard';
import SubCard from 'ui-component/cards/SubCard'
import configData from 'config';
import { makeStyles, useTheme } from '@material-ui/styles';
import AceEditor from "react-ace";
import "ace-builds/src-noconflict/mode-python";
import "ace-builds/src-noconflict/mode-sh";
import "ace-builds/src-noconflict/theme-nord_dark";
import "ace-builds/src-noconflict/ext-language_tools";

import axios from 'axios';
//==============================|| TYPOGRAPHY ||==============================//

const APIPage = () => {
    const [isLoading, setLoading] = useState(true);
    const [dataset, setDataset] = useState({});
    const [selected, setSelected] = useState("python");

    const account = useSelector((state) => state.account);

    let { id } = useParams();
    const scripts = {
        "sh": "curl \'{}\'",
        "python": "import requests\n\nresponse = requests.get(\'{}\').json()"
    };
    
    useEffect(() => {

        const url = configData.API_SERVER+"apis/"+id;
        axios.get(url, { headers: { Authorization: `${account.token}` } })
          .then((response) => setDataset(response.data))
          .then(() => setLoading(false))
      }, []);


    return (
        <div>
            {dataset.title && <Grid container spacing={gridSpacing}> 
                <Grid item xs={12} lg={6}>
                    <SubCard 
                        title={
                        <Typography variant="h1">
                            {dataset.title}
                        </Typography>}>
                            <Typography variant="h4" gutterBottom>
                                {dataset.description}
                            </Typography>
                    </SubCard>
                </Grid>
                <Grid item xs={12} lg={6}>
                    <SubCard>
                        <Grid container spacing={gridSpacing}>
                            <Grid container item xs={12}>
                                <TextField fullWidth id="outlined-basic" label="API Endpoint" variant="outlined" value={dataset.url} disabled />
                            </Grid>
                            <Grid container item xs={12} spacing={gridSpacing}>
                                <Grid item>
                                    <Button onClick={() => {setSelected("python")}} startIcon={<CodeIcon />} variant={selected === "python" ? "contained": "outlined"}>Python</Button>
                                </Grid>
                                <Grid item>
                                    <Button onClick={() => {setSelected("sh")}} startIcon={<CloudIcon />} variant={selected === "sh" ? "contained": "outlined"}>CuRL</Button>
                                </Grid>
                            </Grid>
                            <Grid item xs={12}>
                                <AceEditor
                                    mode={selected}
                                    theme="nord_dark"
                                    width="100%"
                                    height="100px"
                                    editorProps={{ $blockScrolling: true }}
                                    enableLiveAutocompletion
                                    fontSize={16}
                                    value={scripts[selected].replace("{}", dataset.url+"?key="+account.user.api_key)}
                                    sx={{ borderRadius: '12px' }}
                                    readOnly
                                    wrapEnabled
                                />
                            </Grid>
                        </Grid>
                    </SubCard>
                </Grid>
            </Grid>}
        </div>
        );
};

export default APIPage;
